<template>
    <div id="register">
        <!--  -->
        <row class="loginNav">
            <i-col span="6" offset="5">
                <img src="../../assets/logo-black.png" alt="" style="margin-top:5px;">
            </i-col>
            <i-col span="13" style="height:100%;float:right;">
               <span style="float:right;margin-right:20%;line-height:60px;font-size:15px;">
                  <router-link :to="{name:'login'}">
                    
                  </router-link>
               </span>
            </i-col>
        </row>
        <!--  -->
        <div class="container">
            <Tabs active-key="key1">
                <Tab-pane label="" key="key1" class="tab">
                    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
                        <Form-item label="" prop="name">
                            <i-input v-model="formValidate.name" placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="no">
                            <i-input type="text" v-model="formValidate.no" number placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="class">
                            <i-select v-model="formValidate.class" placeholder="">
                                <i-option value="181">181</i-option>
                                <i-option value="182">182</i-option>
                                <i-option value="183">183</i-option>
                                <i-option value="184">184</i-option>
                            </i-select>
                        </Form-item>
                        <Form-item label="" prop="mail">
                            <i-input v-model="formValidate.mail" placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="teacher">
                            <i-select v-model="formValidate.teacher" placeholder="">
                                <i-option value="c"></i-option>
                                <i-option value="w"></i-option>
                                <i-option value="a"></i-option>
                            </i-select>
                        </Form-item>
                        <Form-item label="" prop="passwd">
                            <i-input type="password" v-model="formValidate.passwd" placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="passwdCheck">
                            <i-input type="password" v-model="formValidate.passwdCheck"  placeholder=""></i-input>
                        </Form-item>
                        <Form-item>
                            <i-button type="success" @click="handleSubmit('formValidate')" style="width:80px;margin-left:30px;"></i-button>
                            <i-button type="primary" @click="handleReset('formValidate')" style="width:80px;margin-left:160px;"></i-button>
                        </Form-item>
                     </i-form>
                </Tab-pane>
                <Tab-pane label="" key="key2">
                    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">

                        <Form-item label="" prop="name">
                            <i-input v-model="formValidate.name" placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="no">
                            <i-input type="text" v-model="formValidate.no" number placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="passwd">
                            <i-input type="password" v-model="formValidate.passwd" placeholder=""></i-input>
                        </Form-item>
                        <Form-item label="" prop="passwdCheck">
                            <i-input type="password" v-model="formValidate.passwdCheck"  placeholder=""></i-input>
                        </Form-item>

                        <i-button type="primary" class="btn"></i-button>
                     </i-form>
                </Tab-pane>
            </Tabs>
        </div>
        <!--  -->
        <div class="b-footer">
            Copyright  2019 BiXing Mo All rights reserved. More information  - design by BiXing
        </div>
    </div>
</template>

<script>
// import studentRigister from 'studentRegister'

export default {
    data () {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.formValidate.passwdCheck !== '') {
                        // 
                        this.$refs.formValidate.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassCheck = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.formValidate.passwd) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };

            return {
                formValidate: {
                    name: '',
                    mail: '',
                    teacher: '',
                    no:'',
                    passwd: '',
                    passwdCheck: ''
                },
                ruleValidate: {
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    mail: [
                        { required: true, message: '', trigger: 'blur' },
                        { type:'email', message: '', trigger: 'blur' }
                    ],
                    teacher: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    class: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    passwd: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    passwdCheck: [
                        { validator: validatePassCheck, trigger: 'blur' }
                    ],
                    no: [
                        { required:true,type:"number",message:'',trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$router.push({name:'login'});
                        console.log(555)
                    }else{
                        this.$Message.error('!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
}
</script>

<style scoped>
    .loginNav{
        height: 60px;
        border-bottom: 1px solid rgb(216, 213, 213);
    }
    .container{
        max-width: 1100px;
        height: 600px;
        margin:100px auto;
        border:1px solid rgb(238, 226, 226);
        padding: 20px;
    }
    a{
        color:rgb(81, 90, 110);
    } 
    .tab{
        padding: 20px;
    }
    form{
        width: 500px;
        margin:40px auto;
    }
    .btn{
        margin-left: 80px;
        width: 84%;
    }
    .b-footer{
        width: 100%;
        height: 70px;
        background:rgba(230, 230, 230,0.6);
        text-align: center;
        line-height: 72px;
        /* margin-top: 5px; */
    }
</style>


